.about {
  position: relative;
  background-color: var(--main-bg-color);
  color: var(--light-color);
  &::before {
    position: absolute;
    content: '';
    top: 1px;
    left: -9px;
    transform: rotate(360deg);
    width: 550px;
    height: 424px;
    background-image: url(../../img/about-bg.png);
    background-size:cover;
    object-fit: contain;
    background-repeat: no-repeat;
  }
  &__left {
    padding-top: 220px;
    padding-left: 5px;
    grid-column: 5 span;
  }
  &__title {
    margin: 0;
    margin-bottom: 20px;
    color: var(--light-color);
    font-weight: 700;
    font-size: rem(30);
    line-height: 30px;
  }
  &__text {
    margin: 0;
    margin-bottom: 20px;
    max-width: rem(400);
    font-family: 'Intro Book';
    font-weight: 400;
    font-size: rem(16);
    line-height: 140%;
    color: var(--light-color);
  }

  &__wrapper {
    display: flex;
    flex-direction: column;
    align-items: end;
    margin-right: 35px;
  }

  &__author {
    position: relative;
    font-family: 'Intro Bold';
    font-weight: 700;
    font-size: rem(20);
    line-height: 20px;
    color: var(--light-color);
    &::before {
      position: absolute;
      content: '';
      top: calc(50% - 2px);
      left: -50px;
      background-color: var(--light-color);
      height: 3px;
      width: 30px;
    }
  }
  &__right {
    grid-column: 6 / 5 span;
    margin-right: -20px;
  }
}
